import React from 'react';
import style from './burger-ingredients.module.css';
import Category from '../category/category';
import Tabs from '../tabs/tabs';

import { useInView } from 'react-intersection-observer';
import { useSelector } from '../../services/hooks';

import { TIngredient } from '../../utils/types';

export default function BurgerIngredients() {
    const { items, itemsRequest, itemsFailed } = useSelector(state => state.ingredients);

    const getFilteredIngredients = (category: string) => {
        return items.filter((data: TIngredient) => data.type == category)
    }

    const [ bunRef, inViewBun ] = useInView({
        threshold: 0.2,
    });

    const [ sauceRef, inViewSauce ] = useInView({
        threshold: 0.2,
    });

    const [ mainRef, inViewMain ] = useInView({
        threshold: 0.2,
    });

    return (    
        <div className={style.ingredientList}>
            <Tabs inViewBun={inViewBun} inViewSauce={inViewSauce} inViewMain={inViewMain} />
            {itemsRequest && '...'}
            {itemsFailed && ' '}
            {
                !itemsRequest &&
                !itemsFailed &&
                <>                
                    <div className={`custom-scroll ${style.categoryList}`} id="CommonIngredientList">
                        <Category reference={bunRef} title="" code="bun" list={getFilteredIngredients('bun')} />
                        <Category reference={sauceRef} title="" code="sauce" list={getFilteredIngredients('sauce')} />
                        <Category reference={mainRef} title="" code="main" list={getFilteredIngredients('main')} />
                    </div>
                </>
            }
        </div>
    );
}
